//================= add-font-mixin =====================

@mixin font($font_name, $file_name, $weight, $style) {
    @font-face {
    font-family: $font_name;
    font-display: swap;
    src: url("./fonts/#{$file_name}.woff") format("woff"), url("./fonts/#{$file_name}.woff2") format("woff2");
    font-weight: #{$weight};
    font-style: #{$style};
    }
}

//======================================================


//=============== paste-font-in-mixin ==================

@include font("Montserrat-Bold", "Montserrat-Bold", "700", "normal");
@include font("Montserrat-ExtraLightItalic", "Montserrat-ExtraLightItalic", "200", "italic");
@include font("Montserrat-ExtraLight", "Montserrat-ExtraLight", "200", "normal");
@include font("Montserrat-Light", "Montserrat-Light", "300", "normal");
@include font("Montserrat-Italic", "Montserrat-Italic", "100", "italic");
@include font("Montserrat-Regular", "Montserrat-Regular", "400", "normal");

