//======================= import ========================

@import '../../../vars';

//=======================================================


//======================= styles ========================


.header-links {
    .active {
        color: rgba(255,0,95,.90);
    }
    display: flex;
    justify-content: center;
    align-items: center;
    &__link {
        margin-right: 20px;
        a {
            font-family: $font-bold;
            font-size: 20px;
            text-transform: uppercase;
            color: #ffffff;
            transition: all .3s ease;
        }
        &:last-child {
            margin-right: 0;
        }
    }
}


//=======================================================


//======================= media =========================

@media (max-width: 576px) {
    .header-links {
        &__link {
            a {
                font-size: 15px;
            }
        }
    }
}

@media (min-width: 1200px) {
    .header-links {
        &__link {
            a {
                &:hover {
                    color: rgba(255,0,95,.90);
                }
            }
        }
    }
}

//=======================================================